input[type=text], input[type=email] , input[type=number] {
    width: 50%;
    padding: 5px 22px;
    margin: 10px 0px;
    box-sizing: border-box;  
    border: 2px solid #58B19F;
    border-radius: 50px;

}


textarea {
    width: 50%;
    height: 100px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold   ;
    padding: 15px 22px;
    box-sizing: border-box;
    border: 2px solid #58B19F;
    border-radius: 100px;
      

}

input[type=submit] {

    width: 200px;
    background-color: seagreen;
    border-radius: 50px;
    color: white;
    padding: 8px 36px;
    text-decoration: none;
    border: 2px solid #ccc;
    margin: 5px 4px;
    cursor: pointer;  

}

input[type=text]:hover, input[type=email]:hover , input[type=number]:hover{
    background-color:rebeccapurple ;
}

input[type=submit]:hover{
    background-color:darkblue ;
}
textarea:hover{
    background-color:rebeccapurple ;
}

button:hover{
    background-color:darkblue ;
}
.tex{    width: 40%;
    height: 5000px;
    text-align: center;
    padding: 15px 22px;
    box-sizing: border-box;
    border: 2px solid #58B19F;
    border-radius: 100px;
    float: right;
    margin-top: 0%;
}

.tex2{
    width: 100%;
    height: 400px;
    text-align: center;
    text-transform: initial;
    font-size: 30px;
    font-weight: bold;
    color: blueviolet;
    box-sizing: border-box;
    border: 2px solid #58B19F;
    border-radius: 100px;
    float: right;
    margin-top: 0%;
}

.Right{
    width: 30%;
    
    text-align: center;
    
    box-sizing: border-box;
    border: 2px solid #58B19F;
    border-radius: 100px;
    float: right;
    margin-top: 0%;

}

.Left{
    width: 70%;
    float: left;
}

button{
    width: 100px;
    text-align: center;
    color: red;
    font-weight: bolder;
    background-color:rebeccapurple;
    border-radius: 100px;
    color: white;
    padding: 8px 36px;
    text-decoration: none;
    border: 2px solid #ccc;
    margin: 5px 4px;
    cursor: pointer;  


}

.tex2:hover{
    background-image: url(best.png);
}